.answer {
    background-color: $color-gray;
    border-radius: 15px;
    padding: 10px;
    height: 100%;

    @include _lg {
        margin-top: 20px;
    }

    &-bird {
        display: flex;
        transition: 2s;

        @include _sm {
            flex-direction: column;
        }

        &-img {
            width: 200px;
            height: 155px;
            margin-right: 25px;
            margin-bottom: 25px;

            @include _sm {
                width: 100%;
                text-align: center;
            }

            img {
                width: 200px;
                height: 155px;
                border-radius: 15px;
            }
        }

        &-content {
            width: 100%;
            &-title,
            &-subtitle {
                border-bottom: 0.5px solid $color-violet;
                padding-bottom: 10px;
                margin-bottom: 20px;
            }

            &-title {
                font-size: 24px;
            }

            &-subtitle {
                font-size: 20px;
            }
        }
        &-description {
            font-size: 14px;
        }
    }
}
