.header {
    &-content {
        display: flex;
        width: 100%;
        justify-content: space-between;

        .score {
            padding-top: 60px;
            font-size: 24px;
            font-weight: 800;

            @include _sm {
                font-size: 20px;
                padding-top: 40px;
            }
        }
    }

    &-logo {
        padding-top: 10px;
        img {
            width: 200px;

            @include _sm {
                width: 150px;
            }
        }
    }

    &-list {
        width: 100%;
        padding-top: 10px;
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        text-align: center;

        &-item {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $color-green;
            flex: auto;
            border: 0.5px solid $color-gray-lighten;
            padding: .4rem 1.25rem;
            cursor: pointer;

            &:first-child {
                border-top-left-radius: 10px;

                @include _lg {
                    border-bottom-left-radius: 0px;
                }
            }
            &:last-child {
                border-bottom-right-radius: 10px;

                @include _md {
                    border-bottom-left-radius: 0px;
                }
            }

            &.active {
                background-color: $color-green-lighten;
                font-weight: 700;
            }
        }
    }
}