.answers {
    width: 100%;
    background-color: $color-gray;
    border-radius: 15px;
    height: 100%;

    &-list {
        &-item {
            padding: 0.85rem 1.25rem;
            border-bottom: 1px solid $color-gray-lighten;
            font-weight: 600;
            cursor: pointer;
            transition: 0.2s;

            &:last-child {
                border: none;
                border-bottom-left-radius: 15px;
                border-bottom-right-radius: 15px;
            }
            &:first-child {
                border-top-left-radius: 15px;
                border-top-right-radius: 15px;
            }

            &::before {
                content: "";
                background-color: $color-gray-lighten;
                font-weight: bold;
                display: inline-block;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                margin-right: 10px;
                transition: 0.3s;
            }

            &:hover {
                background-color: $color-gray-lighten;
                &::before {
                    background-color: $color-violet;
                    height: 12px;
                    width: 12px;
                }
            }

            &.right {
                &::before {
                    background-color: $color-green-lighten;
                    height: 12px;
                    width: 12px;
                }
            }
            &.wrong {
                &::before {
                    background-color: $color-red;
                    height: 12px;
                    width: 12px;
                }
            }
        }
    }
}
