.game-over {
    width: 100%;
    background-color: $color-gray;
    border-radius: 20px;
    min-height: 60vh;
    padding: 4rem 2rem;
    text-align: center;

    &-title {
        font-size: 80px;
        font-weight: 200;

        @include _md {
            font-size: 60px;
        }
        @include _sm {
            font-size: 40px;
        }
    }

    &-subtitle {
        font-size: 30px;

        border-bottom: 0.5px solid $color-violet;
        padding-bottom: 30px;

        @include _sm {
            font-size: 24px;
        }
    }

    &-congratulations {
        display: flex;
        justify-content: space-around;

        &-text {
            max-width: 50%;
            display: flex;
            align-items: center;
            color: $color-violet;
            font-weight: 800;
            font-size: 20px;
        }
    }

    &-button {
        margin-top: 50px;
        width: 100%;
        padding: 10px 0;
        background-color: $color-green-lighten;
        border: 1px solid $color-violet;
        outline: none;
        border-radius: 20px;
        color: #ffffff;
        font-weight: 800;

        &:focus {
            outline: none;
            border: 1px solid $color-violet;
        }
    }
}