.next-button {
    background-color: $color-gray;
    border: 1px solid $color-green-lighten;
    margin-top: 20px;
    color: #ffffff;
    font-weight: 800;
    border-radius: 25px;
    outline: none;
    transition: 1s;
    cursor: none;
    
 
        position: relative;
        padding: 10px 0;
        width: 100%;
        font-size: 20px;
        top: 0;
        right: 0;
    

    
    p {
        padding: 0;
        margin: 0;
        font-size: 24px;
    }

    

    @include _lg {
        margin-top: 40px;
    }

    &:focus {
        outline: none;
        border: 1px solid $color-green-lighten;
    }

    &.active {
        background-color: $color-violet;
        cursor: pointer;
    }
}