.bird-question {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $color-gray;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px;

    @include _md {
        flex-direction: column;
    }

    &-image {
        width: 250px;
        height: 155px;
        margin-right: 40px;

        @include _md {
            margin-right: 0;
            margin-bottom: 10px;
        }

        img {
            width: 100%;
            height: 100%;
            border-radius: 15px;
        }
    }

    &-content {
        width: 100%;
        &-title {
            padding-bottom: 20px;
            border-bottom: 1px solid $color-violet;
            margin-bottom: 20px;
        }
    }
}
