
// variables
$color-black: #222222;
$color-gray: #303030;
$color-gray-lighten: #555;
$color-green: #008966;
$color-green-lighten: #00bc8c;
$color-red: #ff0000;
$color-violet: #9e40c4;

//

//

@mixin _xxl {
    @media (max-width: 1365px) {
        @content;
    }
}

@mixin _xl {
    @media (max-width: 1170px) {
        @content;
    }
}

@mixin _lg {
    @media (max-width: 992px) {
        @content;
    }
}

@mixin _md {
    @media (max-width: 768px) {
        @content;
    }
}

@mixin _600 {
    @media (max-width: 599px) {
        @content;
    }
}

@mixin _sm {
    @media (max-width: 480px) {
        @content;
    }
}

@mixin _320 {
    @media (max-width: 321px) {
        @content;
    }
}

@mixin _xs {
    @media (max-width: 361px) {
        @content;
    }
}

// 
@import './Header/Header.scss';
@import './QuestionBlock/Question.scss';
@import './AnswersListBlock/AnswersList.scss';
@import './AnswerBlock/Answer.scss';
@import './NextButton/NextButton.scss';
@import './GameOver/GameOver.scss';

//
::-webkit-scrollbar {
    width: 5px;
  }
  ::-webkit-scrollbar-track {
    background: $color-gray; 
  }
  ::-webkit-scrollbar-thumb {
    background: $color-violet; 
  }
  

* {
    font-display: swap;
}

body {
    background-color: $color-black !important;
    color: #ffffff !important;
    padding-bottom: 10px;
}

/////
audio {
    width: 100%;
    height: 40px;
}

audio::-webkit-media-controls-panel {
    background-color: $color-green-lighten;
}



audio::-webkit-media-controls-timeline-container {
    background-color: $color-green-lighten;
}

audio::-webkit-media-controls-current-time-display {
    color: $color-violet;
    font-weight: 700;
    text-shadow: none;
}

audio::-webkit-media-controls-time-remaining-display {
    color: $color-violet;
    font-weight: 700;
    text-shadow: none;
}

audio::-webkit-media-controls-seek-back-button {
    background-color: $color-violet;
}

audio::-webkit-media-controls-seek-forward-button {
    background-color: $color-violet;
}

audio::-webkit-media-controls-fullscreen-button {
    background-color: $color-violet;
}

audio::-webkit-media-controls-rewind-button {
    background-color: $color-violet;
}

audio::-webkit-media-controls-return-to-realtime-button {
    background-color: $color-violet;
}
/////

h1,
h2,
h3,
h4 {
    margin: 0;
    line-height: 1;
}

p {
    margin: 0;
}

ul,
ol,
li {
    list-style-type: none;
    padding: 0;
}

*:focus {
    outline: none;
    border: none;
}

a {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

.c-0 {
    color: #000000;
}
